/*prawa i lewa strona menu */
.bar-main-menu {
    background-color: #1a084e;
    ;
    height: 100vh;
    width: 20vw;
    position: fixed;
    top: 0;
    z-index: 3;

}


.left-bar-main-menu {
    left: 0;
    clip-path: polygon(0 0,
            /* Górny lewy róg */
            100% 0,
            /* Górny prawy róg */
            75% 50%,
            /* Wcięcie w środku (po prawej) */
            100% 100%,
            /* Dolny prawy róg */
            0 100%
            /* Dolny lewy róg */
        );
}

.right-bar-main-menu {
    right: 0;
    clip-path: polygon(0 0,
            100% 0,
            100% 100%,
            0% 100%,
            25% 50%,
            0% 0%);
}

/*ramki-menu*/

.borrder-menu {
    position: fixed;
    z-index: 2.5;

    height: 50.05vh;
    width: 4px;

    border-right: 2px solid white;
    transform-origin: top;
}

.right-borrder-menu-top {
    top: 0;
    left: 20.1vw;
    transform: skew(-11deg);
}

.right-borrder-menu-bottom {
    bottom: 0;
    left: 20.1vw;
    transform: skew(11deg);
    transform-origin: bottom right;
}

.left-borrder-menu-top {
    top: 0;
    right: 20.2vw;
    transform: skew(11deg);
}

.left-borrder-menu-bottom {
    bottom: 0;
    right: 20.2vw;
    transform: skew(-11deg);
    transform-origin: bottom right;
}

.borrder-menu::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 6px;
    background: #ad15f3;
    top: 0;
    z-index: -1;
    filter: blur(8px);
    pointer-events: none;
}

.right-borrder-menu-top::after {
    left: 100%;
}

.right-borrder-menu-bottom::after {
    left: 100%;
}

.left-bar-main-menu::after {
    right: 100%;
}

.left-bar-main-menu::after {
    right: 100%;
}

